import React from 'react';
import ReactDOM from 'react-dom';
import Animate from 'rc-animate';
import classNames from 'classnames';
import CloseO from '@wind/icons/lib/icons/CloseO';
import InfoCircleF from '@wind/icons/lib/icons/InfoCircleF';
import InfoCircleO from '@wind/icons/lib/icons/InfoCircleO';
import CheckCircleF from '@wind/icons/lib/icons/CheckCircleF';
import CloseCircleF from '@wind/icons/lib/icons/CloseCircleF';
import CloseCircleO from '@wind/icons/lib/icons/CloseCircleO';
import ExclamationCircleO from '@wind/icons/lib/icons/ExclamationCircleO';
import ExclamationCircleF from '@wind/icons/lib/icons/ExclamationCircleF';
import CheckCircleO from '@wind/icons/lib/icons/CheckCircleO';
function noop() { }
export default class Alert extends React.Component {
    constructor(props) {
        super(props);
        this.handleClose = (e) => {
            e.preventDefault();
            let dom = ReactDOM.findDOMNode(this);
            dom.style.height = `${dom.offsetHeight}px`;
            // Magic code
            // 重复一次后才能正确设置 height
            dom.style.height = `${dom.offsetHeight}px`;
            this.setState({
                closing: false,
            });
            (this.props.onClose || noop)(e);
        };
        this.animationEnd = () => {
            this.setState({
                closed: true,
                closing: true,
            });
        };
        this.state = {
            closing: true,
            closed: false,
        };
    }
    render() {
        let { closable, description, type, prefixCls = 'w-alert', message, closeText, showIcon, banner, className = '', style, } = this.props;
        // banner模式默认有 Icon
        showIcon = banner && showIcon === undefined ? true : showIcon;
        // banner模式默认为警告
        type = banner && type === undefined ? 'warning' : type || 'info';
        let AlertIcon;
        // use outline icon in alert with description
        let isO = !!description;
        switch (type) {
            case 'success':
                AlertIcon = isO ? CheckCircleO : CheckCircleF;
                break;
            case 'error':
                AlertIcon = isO ? CloseCircleO : CloseCircleF;
                break;
            case 'warning':
                AlertIcon = isO ? ExclamationCircleO : ExclamationCircleF;
                break;
            case 'info':
            default:
                AlertIcon = isO ? InfoCircleO : InfoCircleF;
                break;
        }
        let alertCls = classNames(prefixCls, {
            [`${prefixCls}-${type}`]: true,
            [`${prefixCls}-close`]: !this.state.closing,
            [`${prefixCls}-with-description`]: !!description,
            [`${prefixCls}-no-icon`]: !showIcon,
            [`${prefixCls}-banner`]: !!banner,
        }, className);
        // closeable when closeText is assigned
        if (closeText) {
            closable = true;
        }
        const closeIcon = closable ? (<a onClick={this.handleClose} className={`${prefixCls}-close-icon`}>
        {closeText || <CloseO />}
      </a>) : null;
        return this.state.closed ? null : (<Animate component="" showProp="data-show" transitionName={`${prefixCls}-slide-up`} onEnd={this.animationEnd}>
        <div data-show={this.state.closing} className={alertCls} style={style}>
          {showIcon ? <AlertIcon className={`${prefixCls}-icon`}/> : null}
          <span className={`${prefixCls}-message`}>{message}</span>
          <span className={`${prefixCls}-description`}>{description}</span>
          {closeIcon}
        </div>
      </Animate>);
    }
}
